import React from "react";

import { Link } from "gatsby";
import Layout from "../components/layout";
import SEO from "../components/seo";

import { graphql } from "gatsby";
import Image from "../components/image";

//引数 data　追加
const Blog = ({ data }) => (
  <Layout>
    <SEO title="Portfolio" />
    <h1>Blog</h1>
    <p>{data.allMarkdownRemark.totalCount} Posts :</p>
    <div className="prodact">
      {/*ループ1　はじめ *********************************************/}
      {data.allMarkdownRemark.edges.map(({ node }) => (
        <div className="item-prodact-thumbnail" key={node.id}>
          <Link to={node.fields.slug}>
            <Image alt="" directory={node.parent.relativeDirectory} filename={node.frontmatter.thumbnail} />
            <div className="prodact-item-text">
              <div className="prodact-item-date">
                {node.frontmatter.date}
              </div>
              <div className="prodact-item-title">
                {node.frontmatter.title}
              </div>
            </div>


            {/*<p>{node.excerpt}</p>*/}
          </Link>
        </div>
      ))}
      {/*ループ1　終わり **********************************************/}
    </div>



  </Layout>
);

export default Blog;
//追加2
export const query = graphql`
  query {
    allMarkdownRemark(filter: {fields: {slug: {regex: "/article/blog/"}}},sort: { fields: [frontmatter___date], order: DESC }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            date(formatString: "YYYY")
            title
            isProduct
            thumbnail {
              base
            }
          }
          fields {
            slug
          }
          excerpt
          parent {
            ... on File {
              relativeDirectory
            }
          }
        }
      }
    }
  }
`;

